.gallery {
  background: linear-gradient(#fff5f6 50%, #FFFFFF 50%);
  
}

.galery__grids {
   display: grid;
   grid-template-columns: repeat(7, 42px);
   grid-auto-rows: 14px;
  // grid-template-columns: repeat(7, 1fr);
  // grid-auto-rows: minmax(auto, 42px);
 
    justify-content: center;
    margin-left: auto;
    margin-right: auto;

      @media screen and (min-width: 480px) {
          grid-template-columns: repeat(7, 66px);
          grid-auto-rows: 22px;
        }

   @media screen and (min-width: 768px) {
       grid-template-columns: repeat(7, 105px);
       grid-auto-rows: 35px;
  }

   @media screen and (min-width: 1280px){
      grid-template-columns: repeat(7, 126px);
      grid-auto-rows: 42px;
      }
}

.galery__grid {
  position: relative;
  grid-gap: 1em;
}

.galery__img {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  // width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  max-width: 100%;
    @media screen and (min-width: 480px) {
        border-radius: 20px;
      }

}

